<template>
  <div id="help">

    <div class="help">
      <Nav page="help" />
      <!-- 正文内容 -->

      <Banner text="使用帮助"/>

      <div class="space40"></div>

      <div class="mainContent">
        
        <div class="shadownItemBox itemBox">

            <div class="animated fadeIn" v-for="(item,index) in list" :key="index" @click="gopage(`/helpDetail?id=${item.id}`)">
              <h1>{{item.title}}</h1>
              <div class="small font12 op04">{{item.addTime}} 创建</div>
              <p>{{item.subtitle || '暂无简介'}}</p>
            </div>

            <div v-if="list.length==0" class="nullData" style="border-bottom:0;"><span>暂无数据</span></div>

        </div>
        <!-- itemBox -->

      </div>
      <!-- mainContent -->

      
    </div>
    <!-- filter -->

    <div class="space40"></div>

    <!-- 正文结束 -->
      <Footer />


  </div>
  <!-- index -->
</template>

<script>
  import {
    noticeNews
  } from '../data/apis';

  import Nav from './nav'
  import Footer from './footer'
  import Banner from '@com/exchange/banner'

  export default {
    data() {
      return {
        list:[],
        query:{
          pageSize:1000,
          pageNum:1,
          type:'news'
        }
      }
    },
    components: {
      Nav,
      Footer,
      Banner
    },
    methods: {
      getData(){
        noticeNews(this.query).then(v=>{
          this.list = v.data.list;
        })
      }//getData
    },
    mounted() {
      this.getData();
    } //mounted
  };

</script>
<style rel="stylesheet/scss" scoped lang="scss">
  @import "../styles/Val";
  .help{
    min-height: calc(100vh - 200px);
  }
  .itemBox {
    >div{
      cursor: pointer;
      &:hover{
        h1{
          color: $them_color;
          text-decoration: underline;
        }
      }
      .showMore{
        margin-top: 10px;
        text-align: right;
        span{
          display: inline-block;
        }
      }
      h1{
        font-size: 20px;
        display: block;
        font-weight: bold;
      }
      p{
        color: $them_color_gray;
        line-height: 2em;
        font-size: 14px;
        margin-top: 10px;
      }
      border-bottom: 1px solid $them_border_color_gray;
      padding: 10px 0;
    }
  }

</style>



